<script>
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from "vue-router";
import axios from "@/api/request";
import { BigNumber } from "bignumber.js";
export default {
  setup() {
    const router = useRouter();
    const state = reactive({
      loading: false,
      mintList: [],
      borrowList: [],
      redeemList: [],
      repayList: [],
      liquidationList: [],
      activeName: 'first',
      transmitList: [],
      date: new Date(),
    });
    onMounted(() => {
      getData();
    });
    // 获取数据
    const getData = () => {
      state.loading = true;
      let data = {
        query: `{
  mintEvents(first: 100 ,orderBy: blockTime, orderDirection: desc) {
    id
    amount
    to
    from
    blockTime
    cTokenSymbol
    underlyingAmount
  }
  redeemEvents(first: 100,orderBy: blockTime, orderDirection: desc) {
    id
    amount
    from
    to
    blockTime
    cTokenSymbol
    underlyingAmount
  }
  borrowEvents(first: 100,orderBy: blockTime, orderDirection: desc) {
    id
    amount
    accountBorrows
    borrower
    blockTime
    underlyingSymbol
  }
  repayEvents(first: 100,orderBy: blockTime, orderDirection: desc) {
    id
    amount
    accountBorrows
    borrower
    blockTime
    underlyingSymbol
    payer
  }
  liquidationEvents(first: 100,orderBy: blockTime, orderDirection: desc) {
    id
    amount
    to
    from
    blockTime
    cTokenSymbol
    underlyingSymbol
    underlyingRepayAmount
  }
}`,
        variables: {},
      };
      axios.post("/sub/subgraphs/name/torches", data).then((res) => {
        state.mintList = res.data.mintEvents;
        state.redeemList = res.data.redeemEvents;
        state.borrowList = res.data.borrowEvents;
        state.repayList = res.data.repayEvents;
        state.liquidationList =  res.data.liquidationEvents;
      });
    };
    const formatAddr = (address) => {
      let start = address.substring(0, 6);
      let end = address.substring(38, 42);
      let addString = start + "...." + end;
      return addString;
    };
    const formatAddrLink = (address) => {
      let link = "https://scan.kcc.io/address/" + address;
      return link;
    };
    const formatTxLink = (tx) => {
      let link = "https://scan.kcc.io/tx/" + tx.slice(0,66);
      return link;
    };
    const formatAnswer = (value) => {
      let price = new BigNumber(value)
          .dividedBy(new BigNumber(10).pow(8))
          .toFixed(8);
      return price;
    };
    const formatDate = (value) => {
      let date = new Date(value * 1000);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
    };
    const toChart = (id) => {
      router.push({ path: "./Chart", query: { id: id } });
    };
    const refresh = () => {
      getData();
      state.date = new Date();
    };
    const handleClick = (tab, event) =>  {
      console.log(tab, event);
    }
    return {
      ...toRefs(state),
      formatDate,
      handleClick,
      getData,
      formatAddr,
      formatAddrLink,
      formatTxLink,
      formatAnswer,
      refresh,
      toChart,
    };
  },
};
</script>

